.nav__link {
  transition: color var(--transition), transform var(--transition);
  padding-top: 10px;
  padding-bottom: 10px;
  position: relative;
}

.nav__link:hover, .nav__link:focus {
  color: var(--button-green);
}

@media screen and (max-width: 1279px) {
