}

.nav__link:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: inherit;
  transition: background-color var(--transition);
  display: block;
  position: absolute;
  bottom: 11px;
}

.nav__link:hover.nav__link:after {
  background-color: var(--button-green);
}

.current-page {
