  margin-top: -38px;
  padding-top: 11px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
}

.header__container.no-transparency {
  -webkit-backdrop-filter: blur(6px);
  backdrop-filter: blur(6px);
  color: var(--yellow-main);
  z-index: 20;
  fill: var(--button-green);
  background-image: linear-gradient(#5e6363, rgba(94, 99, 99, .1));
}

.menu-button {
  transition: fill var(--transition);
  z-index: 3;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: auto;
  display: inline-flex;
}

.menu-button:hover, .menu-button:focus {
  fill: var(--button-green-bright);
}

@media screen and (min-width: 768px) {
