}

.menu-button.is-open .menu-button__icon--cross {
  display: block;
}

.mobile-menu {
  width: 125px;
  background-color: var(--white-color);
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  color: var(--black-color);
  box-shadow: var(--box-shadow);
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition), visibility var(--transition);
  border-radius: 30px 0 30px 30px;
  flex-direction: column;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  display: flex;
  position: absolute;
  top: 65%;
  right: 4%;
}

.mobile-menu.is-open {
  z-index: 999;
  opacity: 1;
  visibility: visible;
}

.menu-button__icon--cross {
  fill: var(--main-text-color);
}

.menu__item {
  background-color: var(--white-color);
  z-index: 999;
  cursor: pointer;
  transition: background-color var(--transition);
}

.menu__list .menu__item:first-child {
  border-radius: 30px 0 0;
}

.menu__list .menu__item:last-child {
  border-radius: 0 0 30px 30px;
}

.btn {
  position: absolute;
  top: 3%;
  right: 4%;
}

.menu__link {
  background-color: var(--white-color);
  z-index: 200;
  cursor: pointer;
  transition: background-color var(--transition);
  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 16px;
  display: block;
}

.menu__link:hover, .menu__link:focus {
  background: var(--button-green-bright);
}

.menu__link.first-item {
  border-radius: 30px 0 0;
}

.menu__link.last-item {
  border-radius: 0 0 30px 30px;
}

